body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, 'Segoe UI', 'Roboto', 'Oxygen',
    'Ubuntu', 'Cantarell', 'Fira Sans', 'Droid Sans', 'Helvetica Neue',
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, 'Courier New',
    monospace;
}
.header{
  background-color: black;
  color: white;
  padding: 20px 70px;
  display: flex;
  justify-content: space-between;
}
.cart-icon .counter{
  height:18px;
  width: 18px;
  background-color: blue;
  border-radius: 50%;
  right: 7px;
  top:-6px;
  align-items: center;
  justify-content: center;
  display: flex;
  position: relative;
}
.cart-icon{
  display: flex;
}
.icon{
  font-size: 22px;
}
img{
  height:160px;
}
.card-wrapper{
  display:inline-flex;
  margin: 40px;
  box-shadow: 0px 0px 7px black;
  width: 320px;
  border-radius: 5px;
}
.image-wrapper{
    width: 130px;
    display: flex;
    align-items: center;
    justify-content: center;
    border-right: 1px solid rgb(201,201,201);
    padding: 10px;
}
.content{
  flex-grow: 1;
  display: flex;
  flex-direction: column;
  gap: 10px;
  padding: 10px;
}
.price{
  margin-bottom: auto;
}
.content button{
  background-color: black;
  color: white;
  border-radius: 3px;
}
.quantity{
  display: flex;
  align-self: flex-end;
  border-radius: 3px;
  border: 1px solid grey;
}
.quantity div{
  height: 22px;
  width: 22px;
  display: flex;
  align-items: center;
  justify-content: center;
}
.quantity>div:first-child, 
.quantity>button{
  background-color:rgb(0,0,111);
  color: white;
  border-radius: 0;
}
.quantity>button:disabled{
  opacity:0.7;
}
main{
  padding: 20px;
  display: flex;
  flex-wrap: wrap;
  min-height: 80vh;
  justify-content: center;
  align-items: center;
  align-content: center;
  gap:50px;
}
.total{
  box-sizing: border-box;
  background-color: gray;
  padding: 17px 30px;
  padding-right: 30px;
  position:fixed ;
  width: 100%;
  text-align: right;
  bottom: 0;
}